<template>
    <div id="homepage">

            <div class="main">
                <div class="logo">
                    <img alt="Logo" src="../assets/mini.png" width="200">
                    <span class="new">v 1.1</span>
                </div>
                <div class="title" @click.passive="isShow = 0; changeTitle(1)" v-text="msg"></div>
                <div v-if="isShow == 0">
                    <div class="bookmark" @click.passive="initExam(0)">模拟考试</div>
                    <!-- <div class="bookmark" @click.passive="initExam(1)">错题回顾</div> -->
                    <div class="bookmark" @click.passive="isShow = 1; changeTitle(0)">顺序练习</div>
                    <div class="bookmark" @click.passive="isShow = 2; changeTitle(0)">顺序看题</div>
                </div>
                <div v-if="isShow == 1 || isShow == 2">
                    <div class="sub-bookmark" @click.passive="initExam(2)">选择题</div>
                    <div class="sub-bookmark" @click.passive="initExam(3)">判断题</div>
                </div>
                <div v-if="isShow == 2">
                    <div class="sub-bookmark" @click.passive="initExam(4)">正确判断</div>
                    <div class="sub-bookmark" @click.passive="initExam(5)">错误判断</div>
                </div>

            </div> 

    </div>
</template>

<script>


export default {
    name: 'HomePage',
    emits: ['initExam'],
    data() {
        return {
            isShow: 0,
            msg: "欢迎使用离线题库"
        }
    },
    methods: {
        changeTitle(kd) {
            switch (kd) {
                case 0:
                    this.msg = ">返回菜单<"
                    break;
                case 1:
                    this.msg = "欢迎使用离线题库";
                    break;
            }
        },
        initExam(kd) {
            if (kd==0 ||kd == 1) {
                if(kd==1){
                    alert("暂不支持错题记录");
                    return;
                }
                this.$emit("initExam", [kd,0]);
                return;
            }
            this.$emit("initExam", [this.isShow+1, kd - (this.isShow > 0 ? 2 : 0)]);
        }
    }
}
</script>

