import { createApp } from "vue";
import App from "./App.vue";
 
import axios from "axios"; // 安装axios后引入
 
const app = createApp(App);
 
app.config.globalProperties.$axios = axios; // 将axios挂载到原型上方便使用
 
app.mount("#app");