<template>

    <div id="main">
        <div id="examTitle">
            <h1 @click.passive="$emit('home')">&lt;&nbsp;&nbsp;离线题库考试系统</h1>
            <div id="timing">
                <div id="timer">{{ time[0] }}:{{ time[1].toString().padStart(2, '0') }}</div>
                <button id="suspend" @click="TimeStop()">||</button>
            </div>
        </div>
        <div id="itemSelection">
            <div id="itemChoose">
                <div v-for="(x, indexx) in quesGroup" :key="indexx">
                    <button
                        @click="showItem = (showItem >= x.position[0] && showItem < x.position[1]) ? -1 : x.position[0]">
                        {{ showItem >= x.position[0] && showItem < x.position[1] ? '-' : '+' }} </button>
                            <p> {{ x.name }} </p>
                            <ul v-if="showItem >= x.position[0] && showItem < x.position[1]">
                                <li :class="getItemClass(indexy + x.position[0])" :key="indexy"
                                    @click="showQues(indexy + x.position[0])"
                                    v-for="(y, indexy) in quesAnswer.slice(x.position[0], x.position[1])">
                                    {{ indexy + x.position[0] + 1 }}
                                </li>
                            </ul>
                </div>
            </div>
            <ul id="markInfo">
                <li>当前<span class="nowAnswer"></span></li>
                <li>未做<span class="nopAnswer"></span></li>
                <li v-show="autoNext">正确<span class="yesAnswer"></span></li>
                <li v-show="autoNext">错误<span class="notAnswer"></span></li>
                <li v-show="!autoNext">已做<span class="hasAnswer"></span></li>
            </ul>
        </div>
        <div id="answer">
            <div id="info">
                <h2>{{ title }}</h2>
            </div>
            <div id="item">
                <p id="describe">{{ stem }}</p>
                <div id="option" v-if="!isTF">
                    <div :class="getOptClass(opt.tag)" v-for="opt in option" :key="opt.tag" @click="choice(opt.tag)">
                        {{ opt.tag }}.{{ opt.option }}
                    </div>
                </div>
                <div id="option" v-if="isTF">
                    <div :class="getOptClass(opt.tag)" v-for="opt in option" :key="opt.tag" @click="choice(opt.tag)">
                        {{ opt.tag }}
                    </div>
                </div>
                <div id="explain" v-if="showAns">
                    <p>【正确答案】</p>
                    <p id="knowledge">{{ answer }}</p>
                </div>
            </div>


            <div id="ctrlBtn">
                <button @click="prev()" id="prevItem">上一题</button>
                <button @click="next()" id="nextItem">下一题</button>
                <!-- <button id="calculator">更新</button> -->
                <!-- <button id="markItem">设置待查</button> -->
                <button @click="submit()" id="submit">交卷</button>
            </div>
        </div>
    </div>

    <div id="blackCover" class="black" v-if="isTimeStop">
        <div>
            <span>提示</span>
            <p id="information">当前考试已暂停<br>点击下方按钮继续答题</p>
            <button id="goOn" @click="isTimeStop = false; Time()">继续</button>
        </div>
    </div>

    <div id="mark" v-if="showMark">
        {{ mark }} 分
    </div>
</template>

<script>
import { getCurrentInstance } from "vue";

export default {
    name: 'ExamPaper',
    emits: ['home'],
    props: {
        set: {
            type: Array,
            required: true
        }
    },
    data() {
        return {
            title: "模拟考试",

            title_kd: ["模拟考试", "错题回顾", "顺序练习", "顺序看题"],
            title_sw: ["", "(选择题)", "(判断题)", "(正确判断)", "(错误判断)"],
            qs_flnm: ["6139", "4641", "4652", "4654"],

            defineShowAns: false,
            autoNext: false,
            isIndexNumber: true,
            showMark: false,

            json: "",
            json_: "",
            firstQues: 0,
            secondQues: 0,
            quesList: [],
            quesGroup: [],// position name
            quesAnswer: [],
            yesAnswer: [],
            time: [120, 0],
            timer: null,
            mark: 0,

            index: 0,
            showItem: 0,
            showAns: false,
            isTF: false,
            isTimeStop: false,
            option: "",
            stem: "stem",
            answer: "answer",
            myChoice: "",
        }
    },
    methods: {
        getItemClass(index) {
            var result = [];
            if (index == this.index)
                result.push("nowAnswer");
            if (this.quesAnswer[index] == null) {
                result.push("nopAnswer");
            } else if (this.autoNext) {
                if (this.quesAnswer[index] == this.yesAnswer[index]) {
                    result.push("yesAnswer");
                } else {
                    result.push("notAnswer");
                }
            } else {
                result.push("hasAnswer");
            }
            return result;
        },
        getOptClass(index) {
            if (index == this.myChoice) {
                if (this.showAns) {
                    return index == this.answer ? "yesChoice" : "notChoice";
                } else {
                    return "nowChoice";
                }
            } else {
                if (this.showAns && index == this.answer && this.myChoice != this.answer) {
                    return "yesChoice";
                } else {
                    return "nopChoice";
                }
            }
        },
        choice(opt) {
            if (!this.showAns) {
                this.myChoice = opt;
                this.quesAnswer[this.index] = opt;
                // console.log(this.quesAnswer[this.index] + " choice " + opt)
                if (this.autoNext) {
                    if (opt == this.answer) {
                        this.next();
                    } else {
                        this.showAns = true;
                    }
                }
            }
        },
        prev() {
            if (this.index - 1 >= 0) {
                this.index -= 1;
            } else {
                alert("当前为第一题，为您跳转到最后一题。");
                this.index = this.firstQues + this.secondQues - 1;
            }
            this.showQues(this.index);
        },
        next() {
            if (this.index + 1 < this.firstQues + this.secondQues) {
                this.index += 1;
            } else {
                alert("当前为最后一题，为您跳转到第一题。");
                this.index = 0;
            }
            this.showQues(this.index);
        },
        submit(force = false) {
            var nopChoice = 0, result = 0, size = this.firstQues + this.secondQues;
            for (var i = 0; i < size; i++) {
                if (this.quesAnswer[i] == null)
                    nopChoice++;
                else if (this.quesAnswer[i] == this.yesAnswer[i])
                    result++;
            }
            if (force) {
                alert("时间耗尽，考试结束，系统自动交卷！");
            } else if (nopChoice && !confirm("您还有题目未完成，确认交卷？") || !nopChoice && !confirm("确认交卷？")) {
                return;
            }
            this.TimeStop();
            this.isTimeStop=false;
            this.mark = result;
            this.showMark = true;
            this.autoNext = true;
            alert("本次考试结束，您的成绩为" + result.toString() + "分。\n您现在可以自由查看错题或者以练习模式完成未做的题目。");
        },
        //Private
        RandomNum(Max) {
            var num = + Math.floor(Math.random() * Max);
            return num;
        },
        calcGroup(max = this.firstQues, size = 50) {
            var result = [];
            for (var i = 0; i < max / size; i++) {
                result[i] = {
                    "position": [i * size, Math.min((i + 1) * size, max)],
                    "name": (i * size + 1).toString() + " - " + (Math.min((i + 1) * size, max)).toString()
                };
            }
            return result;
        },
        showQues(no) {
            // console.log("runShow");
            this.index = no;
            var index = this.isIndexNumber ? no : this.quesList[no];
            if (this.json || this.json_) {
                var thisQus = no < this.firstQues ? this.json.list[index] : this.json_.list[index];
                // console.log(thisQus);
                this.stem = (this.index + 1).toString() + "." + thisQus.stem;
                this.answer = thisQus.answer;
                this.yesAnswer[this.index] = this.answer;
                this.option = thisQus.oplist;
                this.myChoice = this.quesAnswer[this.index];
                this.showAns = (this.myChoice != null && this.autoNext) ? true : this.defineShowAns;
                this.isTF = thisQus.answer == "正确" || thisQus.answer == "错误";
            }
        },
        countDown() {
            if (this.time[1] < 0) {
                if (this.time[0] <= 0) {
                    this.submit(true);
                } else {
                    this.time[0]--;
                    this.time[1] = 59;
                }
            }
        },
        Time() {
            this.timer = window.setInterval(() => {
                this.time[1] -= 1;
                this.countDown();
            }, 1000)
        },
        TimeStop() {
            if (this.timer != null) {
                this.isTimeStop = true;
                window.clearInterval(this.timer);
                this.timer = null;
            }
        }
    },
    mounted: function () {
        // alert(`${this.set[0]},${this.set[1]}`);
        // console.log(`${this.set[0]},${this.set[1]}`);
        this.title = this.title_kd[this.set[0]] + this.title_sw[this.set[1] + (this.set[0] == 0 ? 0 : 1)];
        // this.test();

        const { proxy } = getCurrentInstance();
        Promise.all([
            proxy.$axios.get('./data/' + this.qs_flnm[this.set[1]] + '_.json')
                .then(response => this.json = response.data),
            proxy.$axios.get('./data/' + this.qs_flnm[1] + '_.json')
                .then(response => this.json_ = response.data)
        ]).then(() => {
            switch (this.set[0]) {
                case 0:
                    this.firstQues = this.json.list.length;
                    if (this.json_.list)
                        this.secondQues = this.json_.list.length;

                    var firstRepo = 60;
                    var secondRepo = 40;
                    var a = [], b = [], i, j;
                    for (i = 0; i < firstRepo; i++) {
                        a[i] = this.RandomNum(this.firstQues);
                        for (j = 0; j < i; j++) {
                            if (a[i] == a[j]) {
                                i--;
                                break;
                            }
                        }
                    }
                    for (i = 0; i < secondRepo; i++) {
                        b[i] = this.RandomNum(this.secondQues);
                        for (j = 0; j < i; j++) {
                            if (b[i] == b[j]) {
                                i--;
                                break;
                            }
                        }
                    }
                    this.Time()
                    this.quesList = [...a, ...b];
                    // console.log(this.quesList);
                    this.firstQues = firstRepo;
                    this.secondQues = secondRepo;
                    this.defineShowAns = false;
                    this.autoNext = false;
                    this.isIndexNumber = false;
                    this.quesAnswer.length = firstRepo + secondRepo;
                    this.quesGroup = [{ "position": [0, firstRepo], "name": "选择题" },
                    { "position": [firstRepo, firstRepo + secondRepo], "name": "判断题" }]
                    break;
                case 1:
                    break;
                case 2:
                    this.firstQues = this.json.list.length;
                    this.quesAnswer.length = this.firstQues;
                    this.index = 0;
                    this.defineShowAns = false;
                    this.autoNext = true;
                    this.isIndexNumber = true;
                    this.quesGroup = this.calcGroup();
                    break;
                case 3:
                    this.firstQues = this.json.list.length;
                    this.quesAnswer.length = this.firstQues;
                    this.index = 0;
                    this.defineShowAns = true;
                    this.autoNext = true;
                    this.isIndexNumber = true;
                    this.quesGroup = this.calcGroup();
                    break;
            }
            this.showQues(this.index);
        })
    }
}
</script>

