<template>

    <HomeNavigation v-if="showPage == 3" />
    <div class="homepage">
        <HomePage @initExam="initExam" v-if="showPage == 1" />
    </div>
    <div class="exampaper">
        <ExamPaper @home="showPage = 1" :set="setPass" v-if="showPage == 2" />
    </div>

</template>

<script>
import HomePage from './components/HomePage.vue';
import ExamPaper from './components/ExamPaper.vue';
import HomeNavigation from './components/common/Navigation.vue';

export default {
    name: 'App',
    components: {
        HomePage,
        ExamPaper,
        HomeNavigation
    },
    data() {
        return {
            setPass: [-1, -1],
            showPage: 1
        };
    },
    methods: {
        initExam(event) {
            this.setPass = event;
            this.showPage = 2;
        }
    }
}

</script>


